import React, { Component } from "react";
import BillItem from "../BillItem/BillItem";

class BillListComponent extends Component {
  showBillItem = (bills) => {
    var result = null;
    console.log(bills);
    if (bills.length > 0) {
      result = bills.map((bill, index) => {
        return (
          <BillItem
            key={index}
            bill={bill}
            index={index}
            onDelete={this.props.onDelete}
            updateThanhToan={this.props.updateThanhToan}
          />
        );
      });
    }
    return result;
  };
  render() {
    var { bills } = this.props;
    return (
      <div className="table-responsive">
        <table className="table table-primary">
          <thead>
            <tr>
              <th scope="col">S th t</th>
              <th scope="col">M ha n</th>
              <th scope="col">Tn ti khon</th>
              <th scope="col">Tng tin</th>
              <th scope="col">Ngy to ha n</th>
              <th scope="col">Trng thi ha n</th>
              <th scope="col">Hnh ng</th>
            </tr>
          </thead>
          <tbody>{this.showBillItem(bills)}</tbody>
        </table>
      </div>
    );
  }
}

export default BillListComponent;
