import React, { useState } from "react";
import "./RegisterPage.scss";
import { NavLink, useHistory } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";

import CheckLogin from "../../service/checkLogin";
import { accountsCallApi } from "../../utils/apiCaller";
const RegisterPage = () => {
  const [userName, setUserName] = useState("");
  const [password, setPassword] = useState("");
  const [rePassword, setRePassword] = useState("");
  const [showPass, setShowPass] = useState(false);
  const [loading, setLoading] = useState(false);
  const navi = useHistory();
  const register = async () => {
    if (password.length > 5 && userName.length > 5 && rePassword === password) {
      setLoading(true);
      await accountsCallApi("", "post", {
        username: userName,
        password: password,
      })
        .then((res) => {
          if (res.status === 200) {
            toast.success("ng k thnh cng");
            setTimeout(() => {
              navi.push("/login");
            }, 2000);
          }
          // if (res.status === 201) {
          //   toast.warning("Tn ng nhp  tn ti");
          // }
        })
        .catch(async (error) => {
          await accountsCallApi("getByUsser/", "post", {
            username: userName,
          })
            .then((res) => {
              if (res.status === 200) {
                toast.error("Ti khon  tn ti");
              }
              // if (res.status === 201) {
              //   toast.warning("Tn ng nhp  tn ti");
              // }
            })
            .catch((error) => {
              toast.error("ng k tht bi");
            });
        })
        .finally(() => {
          setLoading(false);
        });
    } else {
      toast.error("ng k khng thnh cng");
    }
  };
  const [checkU, setCheckU] = useState(true);
  const [checkP, setCheckP] = useState(false);
  const [checkRP, setCheckRP] = useState(false);
  const [infoCheckU, setCheckInfU] = useState("");
  const [infoCheckP, setCheckInfP] = useState("");
  const [infoCheckRP, setCheckInfRP] = useState("");

  const checkUserName = (event) => {
    if (event.target.value.replace(/^\s+|\s+$/gm, "").length <= 12) {
      setUserName(event.target.value.replace(/^\s+|\s+$/gm, ""));
    }
    if (event.target.value.replace(/^\s+|\s+$/gm, "").length > 5) {
      setCheckU(true);
      setCheckInfU("* Ti khon hp l");
    } else {
      setCheckU(false);
      setCheckInfU("* Ti khon phi c  di t 6 - 12 k t");
    }
  };
  const checkPassword = (e) => {
    if (e.target.value.replace(/^\s+|\s+$/gm, "").length <= 12) {
      setPassword(e.target.value.replace(/^\s+|\s+$/gm, ""));
      checkPassAndRepass(rePassword, e.target.value.replace(/^\s+|\s+$/gm, ""));
    }
    if (e.target.value.replace(/^\s+|\s+$/gm, "").length > 5) {
      setCheckP(true);
      setCheckInfP("* Mt khu hp l");
    } else {
      setCheckP(false);
      setCheckInfP("* Mt khu phi c  di t 6 - 12 k t");
    }
  };
  const checkRePassword = (e) => {
    if (e.target.value.replace(/^\s+|\s+$/gm, "").length <= 12) {
      setRePassword(e.target.value.replace(/^\s+|\s+$/gm, ""));
      checkPassAndRepass(e.target.value.replace(/^\s+|\s+$/gm, ""), password);
    }
  };
  const checkPassAndRepass = (repas, pas) => {
    if (repas === pas) {
      setCheckRP(true);
      setCheckInfRP("* Mt khu trng khp");
    } else {
      setCheckRP(false);
      setCheckInfRP("* Mt khu khng trng khp");
    }
  };
  if (!CheckLogin()) {
    return (
      <div>
        <ToastContainer />
        <div className="container" style={{ marginTop: "50px" }}>
          <div className="panel panel-primary">
            <div className="box f-c">
              <div className="p-5">
                <div className="panel-heading">
                  <div className="mb-3 pad-7">
                    <div className="form-floating mb-3">
                      <h3
                        className="panel-tittle"
                        style={{ textAlign: "center" }}
                      >
                        ng K
                      </h3>
                    </div>
                    <label htmlFor="userName" className="form-label f-l">
                      Ti khon
                    </label>
                    <input
                      type="text"
                      className="form-control "
                      id="userName"
                      placeholder="Ti khon"
                      value={userName}
                      onChange={(e) => checkUserName(e)}
                    />
                  </div>
                  <label
                    className="t-l f-r checkText"
                    style={{
                      color: checkU ? "green" : "red",
                      fontWeight: "bold",
                    }}
                  >
                    {infoCheckU}
                  </label>
                  <div className="mb-3 pad-7">
                    <label htmlFor="pass" className="form-label f-l">
                      Mt khu
                    </label>
                    <input
                      type={showPass ? "text" : "password"}
                      className="form-control "
                      id="pass"
                      placeholder="Mt khu"
                      value={password}
                      onChange={(e) => checkPassword(e)}
                    />
                  </div>
                  <label
                    className="t-l f-r checkText"
                    style={{
                      color: checkP ? "green" : "red",
                      fontWeight: "bold",
                    }}
                  >
                    {infoCheckP}
                  </label>
                  <div className="form-floating mb-3">
                    <div className="form-check form-switch">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        role="switch"
                        id="flexSwitchCheckDefault"
                        checked={showPass ? true : false}
                        onChange={() => setShowPass(!showPass)}
                      />
                      <label
                        className="form-check-label f-l"
                        htmlFor="flexSwitchCheckDefault"
                      >
                        Hin th mt khu
                      </label>
                    </div>
                  </div>
                  <div className="mb-3 pad-7">
                    <label htmlFor="rePass" className="form-label f-l">
                      Nhp li mt khu
                    </label>
                    <input
                      type={showPass ? "text" : "password"}
                      className="form-control "
                      id="rePass"
                      placeholder="Mt khu"
                      value={rePassword}
                      onChange={(e) => checkRePassword(e)}
                    />
                  </div>
                  <label
                    className="t-l f-r checkText"
                    style={{
                      color: checkRP ? "green" : "red",
                      fontWeight: "bold",
                    }}
                  >
                    {infoCheckRP}
                  </label>
                  <br></br>
                  <div className="form-floating mb-3">
                    {loading ? (
                      <button
                        type="button"
                        className="btn btn-primary w-100"
                        disabled
                        id="register1"
                      >
                        loading
                      </button>
                    ) : rePassword.length > 5 &&
                      userName.length > 5 &&
                      rePassword === password ? (
                      <button
                        type="button"
                        className="btn btn-primary w-100"
                        onClick={() => register()}
                        id="register2"
                      >
                        ng K
                      </button>
                    ) : (
                      <button
                        type="button"
                        className="btn btn-primary w-100"
                        disabled
                        id="register1"
                      >
                        Thng tin cha hp l
                      </button>
                    )}
                  </div>
                  <div className="form-floating mb-3">
                    <NavLink
                      to="/login"
                      // type="button"
                      className="btn btn-primary w-50 f-l"
                      // onClick={() => register()}
                    >
                      ng Nhp
                    </NavLink>
                  </div>
                  {/*<div className="form-floating mb-3 f-r">
                    <a href="/forgetPass">Qun mt khu</a>
                  </div>*/}
                  <br></br>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    navi.push("/");
  }
};

export default RegisterPage;
