import React, { useEffect, useState } from "react";
// import "./LoginPage.scss";
import { NavLink, useHistory } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom/cjs/react-router-dom.min";
import { billCallApi } from "../../../utils/apiCaller";
const BillDetailPage = () => {
  const location = useLocation();
  const searchParams = new URLSearchParams(location.search);
  const mahoadon = searchParams.get("mahoadon");
  const user = searchParams.get("user");
  const navi = useHistory();
  const [bill, setBill] = useState({});
  const account = useSelector((state) => state.account);
  const [loading, setLoading] = useState(true);
  const showBillBody = () => {
    var result = null;
    // console.log(users);
    if (bill.chiTietHoaDon) {
      if (bill.chiTietHoaDon.length > 0) {
        result = bill.chiTietHoaDon.map((billItem, index) => {
          return (
            <>
              <tr>
                <td>{billItem.name}</td>
                <td>{billItem.price}</td>
                <td>{billItem.sale}</td>
                <td>{billItem.quantity}</td>
                <td>
                  {new Intl.NumberFormat("vi", {
                    currency: "VND",
                    style: "currency",
                  }).format(billItem.totalPrice)}
                </td>
              </tr>
            </>
          );
        });
      }
    }
    return result;
  };
  //   const dispatch = useDispatch();
  const loadBill = () => {
    if (loading) {
      billCallApi(
        `getBillByUserAndMaHD?username=${user}&maHoaDon=${mahoadon}`,
        "GET",
        null
      )
        .then((res) => {
          if (res.status === 200) {
            setBill(res.data);
            setLoading(false);
          } else {
            toast.error("Khng c ha n tng ng");
            setLoading(false);
          }
        })
        .catch((err) => {
          toast.error("Khng c ha n tng ng");
          setLoading(false);
        });
    }
  };
  loadBill();
  // useEffect(() => {

  // });

  if (
    (user === account.username && account.role !== 0 && account.logged) ||
    (account.role === 0 && account.logged)
  ) {
    return (
      <div>
        <ToastContainer />
        <div className="container" style={{ marginTop: "50px" }}>
          <div className="panel panel-primary">
            <div className="f-c" style={{ width: "60%" }}>
              <div
                className="panel-heading w-100 border-rounded"
                style={{ display: "inline-block", height: "auto" }}
              >
                <div
                  className="w-80 form-control"
                  style={{ display: "inline-block", height: "auto" }}
                >
                  <div className="form-floating mb-3 pt-50">
                    <h3
                      className="panel-tittle"
                      style={{ textAlign: "center" }}
                    >
                      Chi Tit Ha n M {mahoadon}
                    </h3>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      <strong>Ngi Dng:</strong> {bill.username}
                    </label>
                  </div>
                  <div className="form-floating pt-50 ">
                    <label className="form-check-label f-l form-control">
                      <strong>Ngi Nhn:</strong> {bill.tenNguoiNhan}
                    </label>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      <strong>S in Thoi:</strong> {bill.phoneNumber}
                    </label>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      <strong>a ch:</strong> {bill.diaChi}
                    </label>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      <strong>Loi thanh ton:</strong> {bill.loaiThanhToan}
                    </label>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      <strong>Ngy to ha n:</strong>
                    </label>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      {bill.ngayHoaDon
                        ? bill.ngayHoaDon.toLocaleString()
                        : bill.ngayHoaDon}
                    </label>
                  </div>
                  <div className="form-floating pt-50">
                    <label className="form-check-label f-l form-control">
                      <strong>Tnh trng:</strong>{" "}
                      {bill.tinhtrang ? " thanh ton" : "Cha thanh ton"}
                    </label>
                  </div>
                  <div className="form-floating pt-50 mb-3">
                    <label className="form-check-label f-l form-control">
                      <strong>Tng thnh tin: </strong>
                      {new Intl.NumberFormat("vi", {
                        currency: "VND",
                        style: "currency",
                      }).format(bill.tongTien)}
                    </label>
                  </div>
                  {/* <div className="col"> */}
                  {/* <div className="panel panel-primary"> */}
                  {/* <div className="panel-body"> */}
                  <div
                    className="form-floating mb-3 table-responsive form-control"
                    style={{ display: "inline-block", height: "auto" }}
                  >
                    <table
                      className="table table-primary "
                      style={{
                        // display: "inline-block",
                        display: "table-cell",
                        textAlign: "center",
                        height: "auto",
                        width: "100%",
                        margin: "auto",
                      }}
                    >
                      <thead>
                        <tr>
                          <th scope="col">Tn sn phm</th>
                          <th scope="col">Gi Gc</th>
                          <th scope="col">Gim gi</th>
                          <th scope="col">S lng</th>
                          <th scope="col">Tng tin</th>
                        </tr>
                      </thead>
                      <tbody>{showBillBody()}</tbody>
                    </table>
                  </div>
                  {/* </div> */}
                  {/* </div> */}
                  {/* </div> */}
                </div>
                <br></br>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  } else {
    navi.push("/");
  }
};

export default BillDetailPage;
